const countryCellRenderer = (data) => {
  return <div style={{height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
    <SvgDisplayComponent width={40} height={40} alt="" svgData={placeHolderFlag} />
  </div>
}

const categoryCellRenderer = (data) => {
  return <div className="pf_listCategoryCont">
    {data.cellData && data.cellData.map((cdata, index)=>{
      return <Typography key={index} variant="p"> {cdata} </Typography>
    })}
  </div>
}

const statusCellRenderer = (data) => {
  return <div className="pf_listCenterItem">
    <Typography variant="p"> {data.cellData} </Typography>
  </div>
}

const minCellRenderer = (data) => {
  return <div className="pf_listCenterItem">
    <Typography variant="p"> {data.cellData} </Typography>
  </div>
}

const secMarkCellRenderer = (data) => {
  return <div className="pf_listCenterItem">
    <Typography variant="p"> {data.cellData} </Typography>
  </div>
}

const syncCellRenderer = (data) => {
  return <div className="pf_listCenterItem">
    <SvgDisplayComponent width={30} height={30} alt="" svgData={syncSvg} />
  </div>
}

const protectionCellRenderer = (data) => {
  return <div className="pf_listProtectionCont">
    {data.cellData && data.cellData.map((cdata, index)=>{
      if (cdata === 'BuyBack Guarantee') {
        return <SvgDisplayComponent key={index} width={32} height={32} alt="" svgData={buybackSvg} />
      }
      else if (cdata === 'Collateral') {
        return <SvgDisplayComponent key={index} width="32" height="32" alt="" svgData={collateralSvg} />
      }
      else if (cdata === 'Provision Fund') {
        return <SvgDisplayComponent key={index} width="32" height="32" alt="" svgData={fundSvg} />
      }
      else if (cdata === 'Personal Guarantee') {
        return <SvgDisplayComponent key={index} width="32" height="32" alt="" svgData={personalSvg} />
      }
      return null;
    })}
  </div>
}

const logoCellRenderer = (data) => {
  const cid = data.rowData.contact_id;
  return <div className="pf_listCenterItem">
      <PersistedIconComponent width={110} height={50} category="CONTACT" uid={cid} />
    </div>
}

const PlatformsList = ({list, width, height}) => {

  if (list) {
    return (
      <div className="pf_listContainer" style={{height: height}}>
        <Table
          width={width}
          height={height}
          headerHeight={40}
          rowHeight={60}
          // sort={this._sort}
          // sortBy={this.state.sortBy}
          // sortDirection={this.state.sortDirection}
          rowCount={list.length}
          rowGetter={({ index }) => list[index]}
          headerClassName="pf_listHeader"
          rowClassName="pf_listRow"
          gridClassName="pf_listGrid"
          // rowRenderer={(rowData) => {
          //   console.log("RowData ", rowData)
          //   // if (rowData.index % 2 === 0) {
          //   //   rowData.className = rowData.className + " darkRow";
          //   // }
          //   // return rowData;
          // }}
        >
          <Column label="Status" dataKey="status" minWidth={100} width={100} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}} cellRenderer={statusCellRenderer}/>
          <Column label="Logo" dataKey="logo" minWidth={120} width={120} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}}  cellRenderer={logoCellRenderer}/>
          <Column label="Country" dataKey="country" minWidth={80} width={80} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}} cellRenderer={countryCellRenderer} />
          <Column label="Category" dataKey="category" minWidth={170} width={170} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}} cellRenderer={categoryCellRenderer} />
          <Column label="Min" dataKey="min_investment" minWidth={120} width={120} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}} cellRenderer={minCellRenderer} />
          <Column label="Protection" dataKey="protection_scheme" minWidth={150} width={150} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}} cellRenderer={protectionCellRenderer}/>
          <Column label="Sec. Market" dataKey="secondary_market" minWidth={100} width={100} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}} cellRenderer={secMarkCellRenderer} />
          <Column label="Sync" dataKey="sync" minWidth={70} width={70} headerClassName="pf_listHeaderItem" className="pf_listItem" headerStyle={{margin: '0px'}} style={{margin: '0px'}} cellRenderer={syncCellRenderer}/>
        </Table>
      </div>
    )
  }
  else {
    return (
      <div className="pf_listContainer">

      </div>
    )
  }
}

export default PlatformsList
